@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}

html, body {
  height: 100%;
  margin: 0;
}

:root {
  --placeholder-green: #789c33;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}

.unselectable {
  -ms-user-select: none;
      user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
}

.about
{
  background-color: white;
}

.about-container {
  color: rgb(102, 102, 102);
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: calc(max(2vh, 20px));
  line-height: calc(max(2vh, 1.5rem));
  margin: 14vw 10vw;
  text-align: left;
}

/* Screens > 1000px wide */
@media screen and (min-width: 1000px) {
  .about-container {
    margin: 14vw;
    line-height: normal;
  }
}

/* Screens > 2000px wide */
@media screen and (min-width: 2000px) {
  .about-container {
    margin: 14vw 20vw;
    line-height: normal;
  }
}

.project-card {
  display: grid;
  margin: 10px;
  padding: 10px;
  max-width: 600px;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-template-columns: 60fr 40fr;
  align-items: center;
  border: 1px solid rgb(216, 216, 216);
  border-radius: 5px;
}

.card-title {
  font-weight: 600;
}

.left-half {
  grid-column: 1;
}

.right-half {
  padding: 10px;
  grid-column: 2;
}

.card-img {
  border-radius: 5px;
  width: 100%;
}

.card-title, .card-subtitle {
  margin: 5px 0;
}

.card-title {
  font-size: 15px;
}
.card-body, .card-subtitle, .card-link {
  font-size: 14px;
}

.card-subtitle {
  font-style: italic;
}

.card-body, .card-title, .card-subtitle, .card-link {
  font-family: "Roboto", sans-serif;
  text-align: left;
}

.card-link {
  color: rgb(0, 144, 137);
  font-weight: 500;
  text-decoration: none;
  border-bottom: 2px solid rgba(68, 207, 200, 0.411);
}

/* Screens < 600px wide */
@media screen and (max-width: 600px) {
  .card-title {
    font-size: 17px;
  }
  .card-body, .card-subtitle, .card-link {
    font-size: 15px;
  }
  .project-card {
    grid-template-columns: 1fr;
    grid-template-rows: auto 40fr;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
  }
  .left-half {
    grid-column: 1;
    grid-row: 1;
  }
  .right-half {
    grid-column: 1;
    grid-row: 2;
  }
}

/* Screens > 1600px wide */
@media screen and (min-width: 1600px) {
  .project-card {
    max-width: 1000px;
    padding: 30px;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    margin: 20px;
    grid-template-columns: 60fr 40fr;
  }

  .card-body, .card-title, .card-subtitle, .card-link {
    font-size: large;
  }
}

.projects-container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  margin: 3vw 0 0 0;
}

/* Screens > 2000px wide */
@media screen and (min-width: 2000px) {
  .projects-container {
    margin: 0 auto;
    width: 70%;
    grid-template-columns: 1fr 1fr;
  }
}

.projects
{
  background-color: white;
}

.contact-container {
  color: rgb(129, 129, 129);
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: calc(max(3vh, 20px));
  line-height: 3rem;
  padding-top: 15vh;
  text-align: center;
}

.contact-container span {
  color: rgb(49, 49, 49);
  font-weight: 600;
}

.contact-container span:first-of-type {
  text-align:right
}

a {
  color: rgb(129, 129, 129);
  text-decoration: none;
}

/* Screens > 1000px wide */
@media screen and (min-width: 1000px) {
  .contact-container {
    font-size: 2.5rem;
    line-height: 4rem;
    padding-top: 10%;
    text-align: center;
  }

  .contact-container span {
    width: 300px;
    display: inline-block;
    text-align: left;
  }
}

/* Screens > 2000px wide */
@media screen and (min-width: 2000px) {
  .contact-container {
    font-size: 2.5rem;
    line-height: 4rem;
    padding-top: 10%;
    text-align: center;
  }

  .contact-container span {
    width: 300px;
    display: inline-block;
    text-align: left;
  }
}

#nav-container
{
  color: white;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  padding: 6vw;
  position: fixed;
  right: 0;
  z-index: 1;
}
#title
{
  align-items: flex-end;
  box-sizing: border-box;
  color: white;
  display: flex;
  font-family: 'Rubik', sans-serif;
  font-size: 15vw;
  height: 100%;
  justify-content: left;
  line-height: 13vw;
  margin: 0;
  padding: 10vw;
  position: relative;
  text-align: left;
}
.home
{
  background-color: var(--placeholder-green);
  box-sizing: border-box;
  height: 100%;
}
.nav-link
{
  color: white;
  font-size: 4.5vw;
  padding: 0 2vw;
  text-decoration: none;
}
#home-video
{
  box-sizing: border-box;
  height: calc(100vh - calc(100vh - 100%));
  left: 0;
  margin: 0;
  object-fit: cover;
  position: fixed;
  top: 0;
  width: 100vw;
}

/* Screens > 1000px wide */
@media screen and (min-width: 1000px) {
  #title {
    font-size: 10rem;
    line-height: 9rem;
    padding: 5rem;
  }

  .nav-link {
    font-size: 2rem;
    padding: 0 1rem;
  }

  #nav-container {
    padding: 4rem;
  }
}

/* Screens > 2000px wide */
@media screen and (min-width: 2000px) {
  #title {
    font-size: 12rem;
    line-height: 11rem;
    padding: 7rem;
  }

  .nav-link {
    font-size: 1.5rem;
  }

  .blur {
    -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -o-filter: blur(10px);
    -ms-filter: blur(10px);
    filter: blur(10px);
  }
}

.secondary-header {
  margin: 4vw 5vw;
  text-align: left;
}

.header-link {
  color: rgb(202, 202, 202);
}

.header-title {
  animation: GradientShift 4s ease infinite;
  -moz-animation: GradientShift 4s ease infinite;
  -webkit-animation: GradientShift 4s ease infinite;
  -webkit-background-clip: text;
  background-image: linear-gradient(104deg, #049bb0, #a5bb43);
  background-size: 200% 200%;
  color: var(--placeholder-green);
  -webkit-text-fill-color: transparent;
}

h1 {
  display: inline-block;
  font-family: "Rubik", sans-serif;
  font-size: 5vw;
  margin: 0;
  padding-left: 2vw;
}

a {
  text-decoration: none;
}

a:hover {
  cursor: pointer;
}

#back-arrow {
  display: inline-block;
  height: 3vw;
  padding-right: 1vw;
}
@keyframes GradientShift {
  0% {
    background-position: 0% 65%;
  }
  50% {
    background-position: 100% 36%;
  }
  100% {
    background-position: 0% 65%;
  }
}

/* Screens > 1000px wide */
@media screen and (min-width: 1000px) {
  .secondary-header {
    margin: 2vw 3vw;
  }

  #back-arrow {
    height: 2vw;
  }

  h1 {
    font-size: 3vw;
  }
}

/* Screens < 600px wide */
@media screen and (max-width: 600px) {
  .secondary-header {
    margin: 5vw 7vw;
  }

  #back-arrow {
    height: 6vw;
  }

  h1 {
    font-size: 10vw;
  }

  h1:not(.header-title) {
    display: none;
  }
}

